
body {
	margin: 10px;
	padding: 10px;
}

.big-container {
	overflow-x: scroll;
	padding: 1rem;
	border-left: 1px solid rgba(240, 0, 220, 0.3);
	border-right: 1px solid rgba(240, 0, 220, 0.3);

}

.big-container::-webkit-scrollbar {
  display: none;
}

.card-container {
	display: flex;
	flex-flow: row;
	height: 25vh;
}

.card-item {
	margin: 0.3rem;
	padding: 0.1rem;
	overflow-y: auto;
	border-radius: 0.4rem;
	text-align: center;
	background-color: rgba(113, 128, 217, 0.1);
	background-clip: content-box;
	z-index: 2;
}


/* pantallas mayoes a */
@media only screen and (min-width: 768px) {
	.card-item {
		min-width: 20rem;
		max-width: 20rem;
	}

	.card-container {
		height: 25vh;
	}
}

/* pantallas menores a */
@media only screen and (max-width: 767px) {
	.card-item {
		min-width: 15rem;
		max-width: 15rem;
	}

	.card-container {
		height: 30vh;
	}
}

.card-item::-webkit-scrollbar {
  display: none;
}

.sup-title {
	font-size: 30px;
	font-weight: bold;
	text-align: center;
	margin-bottom: 0px;
}

.title {
	font-size: 20px;
	font-weight: bold;
	margin: 0.5rem;
}

.text {
	font-size: 12px;
	color: #666;
	padding: 0.3rem;
}

.space {
	min-width: 1rem;
}

